.styles__Banner___38E9f {
    height: 100vh;
    min-height: 500px;
    padding: 15px;
    color: #fff;
    text-shadow: 0 0 10px #000;
    text-align: center;
    text-transform: uppercase;
    background: url('/assets/images/banner.jpg') center no-repeat;
    background-size: cover;
    background-attachment: fixed;
    align-items: center;
    justify-content: center;
    display: flex;
}

.styles__Banner___38E9f h1, .styles__Banner___38E9f h2 {
    margin: 0;
}

.styles__Banner___38E9f h1 {
    margin-bottom: 10px;
    font-size: 100px;
    font-weight: bold;
}

.styles__Banner___38E9f h2 {
    font-size: 30px;
}

@media screen and (max-width: 767px) {
    .styles__Banner___38E9f h1 {
        font-size: 30px;
    }

    .styles__Banner___38E9f h2 {
        font-size: 16px;
    }
}
