.styles__Toggle___cC-n5 {
    height: 40px;
    padding: 0 10px;
    border: 1px solid #eee;
    border-radius: 5px;
    cursor: pointer;
    background-color: transparent;
    background-color: initial;
    outline: none;
    display: none;
}

.styles__Toggle___cC-n5:hover {
    background-color: #eee;
}

.styles__Toggle___cC-n5 span {
    width: 28px;
    height: 2px;
    background-color: #000;
    display: block;
}

.styles__Toggle___cC-n5 span + span {
    margin-top: 4px;
}

@media screen and (max-width: 767px) {
    .styles__Toggle___cC-n5 {
        display: inline;
        display: initial;
    }
}